.contacts__modal-map {
  position: absolute;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 12px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 95vw;
  height: 95vh;
  background: var(--color-design-white);
  padding: 45px 5px 20px;

  @media screen and (min-width: 480px) {
    width: 90vw;
  }

  @media screen and (min-width: 768px) {
    width: 80vw;
    height: 85vh;
  }

  @media screen and (min-width: 1280px) {
    width: 60vw;
    height: 75vh;
  }
}
.contacts__modal-close.btn-dark {
  position: absolute;
  top: 8px;
  right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;

  width: 30px;
  height: 30px;
  border-color: transparent;
  border-radius: 50%;
  background-color: var(--color-design-white);
  transition: background-color 250ms linear;

  &:hover,
  &:focus {
    background-color: var(--bg-btn-dark);
  }
}

.contacts__modal-map-svg {
  width: 14px;
  height: 14px;
  fill: var(--bg-btn-dark);
  transition: fill 250ms linear;

  .contacts__modal-close.btn-dark:hover &,
  .contacts__modal-close.btn-dark:focus & {
    fill: var(--bg-btn-light);
  }
}
.iframe {
  width: 100%;
  height: 100%;
}
